.navigation {
	display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 45px;
}

.logo-text {
	display: none;
  font-weight: 500;
  font-size: 30px;
  line-height: 0.89;
	color: $color-main;
	transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

	@media screen and (min-width: 768px) {
		display: inline;
	}
}
.svg {
	stroke: $color-main;
}

.logo-text:hover {
	color: $color-accent;
}

.form {
	position: relative;
	margin: 0px auto;

	&__input {
		background: transparent;
		border: none;
		border-bottom: 1px solid $color-main;
		width: 280px;
		color: $color-main;
		&:hover,
		&:focus,
		&:active {
			outline: none;
			border-bottom: 1px solid $color-accent;
			color: $color-accent;
		}
		@media screen and (min-width: 768px) {
			width: 336px;
		}
	}
}

.button {
	padding: 0;
	cursor: pointer;
	color: $color-main;
	background-color: transparent;
	border: none;
	&:focus {
		outline-color: $color-accent;
	}

	&__home {
		position: relative;
		line-height: 1.16;
		margin-right: 20px;

		@media screen and (min-width:768px) {
			margin-right: 40px;
		}
	}
	&__home:hover {
		color: $color-accent;
		transition: 0.3s;
	}
		&__myLibrary {
		position: relative;
		line-height: 1.16;
		margin-right: 20px;

		@media screen and (min-width:768px) {
			margin-right: 40px;
		}

		@media screen and (min-width:1024px) {
			margin-right: 70px;
		}

	}
	&__myLibrary:hover {
		color: $color-accent;
		transition: 0.3s;
	}

	&__submit {
		position: absolute;
		left: 265px;

		&:focus .svg {
			stroke: $color-accent;
		}
		&:hover .svg {
			stroke: $color-accent;
		}

		@media screen and (min-width: 768px) {
			left: 320px;
		}
	}

